
html {
    height: 100%;
}
body {
    font-family: Helvetica, sans-serif;
    max-width: 1200px;
    height: 100%;
}

#root {
    min-height: 80%;
}

#closing {
    position: relative;
    bottom: -20px;
    width: 100%;
    font-size: 90%;
    text-align: center;
}
#closing li {
    padding: 2px;
    margin: 0px;
    height: 1.5em;
    list-style-type: none;
}
